    align=\"center\"\r\n                         prop=\"phone\">\r\n        </el-table-column>\r\n        <el-table-column label=\"注册类型\"\r\n                         align=\"center\"\r\n                         prop=\"type\">\r\n        </el-table-column>\r\n        <el-table-column label=\"注册地区\"\r\n                         align=\"center\"\r\n                         prop=\"area\">\r\n\r\n        </el-table-column>\r\n\r\n        <el-table-column label=\"详细地址\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            <el-popover placement=\"top-start\"\r\n                        width=\"200\"\r\n                        trigger=\"hover\"\r\n                        :content=\"`${scope.row.province}${scope.row.city}${scope.row.county}${scope.row.address}`\">\r\n              <el-button slot=\"reference\"\r\n                         type=\"text\"\r\n                         class=\"ellipsis\">{{`${scope.row.province}${scope.row.city}${scope.row.county}${scope.row.address}`}}</el-button>\r\n            </el-popover>\r\n          </template>\r\n        </el-table-column>\r\n        <el-table-column label=\"申请时间\"\r\n                         align=\"center\"\r\n                         prop=\"create_time\">\r\n\r\n        </el-table-column>\r\n\r\n        <el-table-column label=\"操作\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            <el-button type=\"primary\"\r\n                       size=\"mini\"\r\n                       :loading=\"throughLoading[scope.$index]\"\r\n                       @click=\"through(scope.row)\">通过</el-button>\r\n            <el-button type=\"danger\"\r\n                       size=\"mini\"\r\n                       :loading=\"rejecteLoading[scope.$index]\"\r\n                       @click=\"rejecte(scope.row)\">驳回</el-button>\r\n          </template>\r\n\r\n        </el-table-column>\r\n      </el-table>\r\n\r\n      <!-- 分页 -->\r\n      <div class=\"page_center\"\r\n           v-show=\"list && list.length>0\">\r\n        <paging :page-count=\"pageCount\"\r\n                :page=\"page\"\r\n                @index=\"paging\"></paging>\r\n        <!--分页的组件-->\r\n      </div>\r\n    </div>\r\n\r\n  </div>\r\n</template>\r\n<script type=\"text/ecmascript-6\">\r\nimport Viewer from '@/utils/Viewer'\r\nimport { getPro } from '@/utils'\r\nimport region from '@/components/common'\r\nexport default {\r\n  data () {\r\n    return {\r\n      list: [],\r\n      token: window.sessionStorage.getItem('bbytoken'),\r\n      page: 1,\r\n      pageCount: 1,\r\n      thCurId: '',\r\n      authList: [],\r\n      threeAuthList: [],\r\n      throughLoading: [],\r\n      rejecteLoading: [],\r\n\r\n    }\r\n  },\r\n\r\n  methods: {\r\n    paging (e) {\r\n      this.page = e;\r\n      this.init()\r\n    },\r\n\r\n    init () {\r\n      this.$axios.post('admin/DeTravelExamine/regAudit', { token: this.token, page: this.page, audit_status: 0 })\r\n        .then(res => {\r\n          this.list = res.data.data.list;\r\n          this.pageCount = res.data.data.rows;\r\n        }).catch(err => { })\r\n    },\r\n\r\n\r\n    //通过\r\n    through (item, index) {\r\n\r\n      this.$confirm('此操作将通过审核, 是否继续?', '提示', {\r\n        confirmButtonText: '确定',\r\n        cancelButtonText: '取消',\r\n        type: 'warning',\r\n        center: true\r\n      }).then(async () => {\r\n        try {\r\n\r\n          this.throughLoading[index] = true\r\n          const res = await this.$axios.post('admin/DeTravelExamine/regThrough', { token: this.token, id: item.id, phone: item.phone, tour_name: item.tour_name })\r\n          this.throughLoading[index] = false\r\n          if (res.data.code == 1) {\r\n            this.$message({ message: res.data.msg, type: \"success\" })\r\n            this.init()\r\n          } else {\r\n            this.$message.error(res.data.msg)\r\n          }\r\n        } catch (err) {\r\n          this.throughLoading[index] = false\r\n          throw (err)\r\n        }\r\n      }).catch(() => { });\r\n    },\r\n    //驳回\r\n    rejecte (item, index) {\r\n\r\n      this.$prompt('请输入驳回理由', '提示', {\r\n        confirmButtonText: '确定',\r\n        cancelButtonText: '取消',\r\n        inputPattern: /\\S/,\r\n        inputErrorMessage: '请输入驳回理由'\r\n      }).then(async ({ value }) => {\r\n        try {\r\n\r\n          this.rejecteLoading[index] = true\r\n          const res = await this.$axios.post('admin/DeTravelExamine/regDismissal', { token: this.token, id: item.id, reason: value, phone: item.phone, tour_name: item.tour_name })\r\n          this.rejecteLoading[index] = false\r\n          if (res.data.code == 1) {\r\n            this.$message({ message: res.data.msg, type: \"success\" })\r\n            this.init()\r\n          } else {\r\n            this.$message.error(res.data.msg)\r\n          }\r\n        } catch (err) {\r\n          this.rejecteLoading[index] = false\r\n          throw (err)\r\n        }\r\n      }).catch(() => { });\r\n    },\r\n\r\n    Auth () {\r\n      var id = this.$route.query.id;\r\n      this.curId = id;\r\n      this.$axios.post('admin/Auth/erAuth', {\r\n        token: window.sessionStorage.getItem('bbytoken'),\r\n        id: id\r\n      })\r\n        .then(res => {\r\n\r\n          if (res.data.code == 1) {\r\n            var arr = res.data.data;\r\n            for (var i = 0; i < arr.length; i++) {\r\n              if (arr[i].son) {\r\n                if (arr[i].name == '注册审核') {\r\n                  this.seCurId = arr[i].id;\r\n                  this.threeAuthList = arr[i].son;\r\n                }\r\n                for (var j = 0; j < arr[i].son.length; j++) {\r\n                  if (arr[i].action != arr[i].son[j].action) {\r\n                    arr[i].action = arr[i].son[0].action;\r\n                  }\r\n                  if (arr[i].son[j].name == '未审核' && arr[i].name == '注册审核') {\r\n                    this.thCurId = arr[i].son[j].id;\r\n                  }\r\n                }\r\n              }\r\n            }\r\n            this.authList = arr;\r\n          } else {\r\n            this.$alert(res.data.msg, '提示', { type: 'error' });\r\n          }\r\n        })\r\n        .catch(err => {\r\n          alert(err);\r\n        })\r\n    }\r\n  },\r\n  mounted () {\r\n    this.Auth()\r\n    this.init()\r\n  },\r\n}\r\n</script>\r\n<style scoped>\r\n.ellipsis {\r\n  width: 100px;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-box-orient: vertical;\r\n  -webkit-line-clamp: 2;\r\n  margin: 0 auto;\r\n  text-align: center;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

